import React from 'react'
import Link from 'gatsby-link'
import styled, { keyframes } from 'styled-components'
import cssDoodle from 'css-doodle'

const slideIn = keyframes`
  to {
    height: 25vmax;
  }
`

const AppContainer = styled.div`
  position: relative;
  background-color: white;
  font-size: 1.1rem;
  margin: -10rem auto 0;
  max-width: 50rem;
  padding: 2rem 2rem;
  z-index: 50;
`

const FlexContainer = styled.div`
  display: flex;
`

const Title = styled.h1`
  font-size: 2.8rem;
  line-height 3.6rem;
  margin-bottom: 0;
  & a {
    color: black;
  }

  @media (max-width: 50rem) {
    margin-top: 0;
  }
`

const DoodleContainer = styled.div`
  height: 100vh;
  animation: ${slideIn} .5s ease-in-out forwards;
  overflow: hidden;
`

const doodleConfig = `
  :host {
    z-index: 1;
    position: relative;
  }

  :doodle {
    @grid: 10 / 100vw; 
    @min-size: 30em;
  }
  
  background: @pick(
    rgba(1, 186, 239, 1),
    rgba(31, 32, 65, 1),
    #EBBAB9,
    rgba(17, 157, 164, 1),
    rgba(25, 100, 126, 1)
  );
  
  border-radius: @pick(
    100% 0 0 0,
    0 100% 0 0,
    0 0 100% 0,
    0 0 0 100%
  );
`

class Layout extends React.Component {
  render() {
    const { location, children } = this.props
    let header

    let rootPath = `/`
    if (typeof __PREFIX_PATHS__ !== `undefined` && __PREFIX_PATHS__) {
      rootPath = __PATH_PREFIX__ + `/`
    }

    if (location.pathname === rootPath) {
      header = (
        <FlexContainer>
          <Title>
            <Link to={'/'}>
              @sliptype
            </Link>
          </Title>
        </FlexContainer>
      )
    } else {
      header = (
        <h3>
          <Link to={'/'}>
            @sliptype
          </Link>
        </h3>
      )
    }
    return (
      <div>
        <DoodleContainer>
          <css-doodle>{ doodleConfig }</css-doodle>
        </DoodleContainer>
        <AppContainer>
          { header } 
          { children }
        </AppContainer>
      </div>
    )
  }
}

export default Layout
